body {
  margin: 0px;
  padding: 0px;
  font-family: roboto;
  /* background-image: url(../images/bg1.jpg); */
  /* background-image: url(https://media.istockphoto.com/photos/summer-forest-with-sun-light-nature-background-picture-id1018214882); */
}

.title_in_note{
  font-size: 16px;
  font-weight: 600;
  letter-spacing: 1px;
  margin-bottom: 10px; 
 }

 .content_div{
  font-size: 14px;
  color:#444;
  white-space: pre-wrap;

}

.card_field_input {
  width: 200px;
  box-sizing: border-box;
  padding: 2px;
}

.top_card {
  color:#333;
  border-radius: 4px;
  box-shadow: 2px 2px 5px #888888;
  background-color:#fff;
  text-align: center;
  /*margin:  10px;*/
  margin:  20px 0px;
  padding: 20px;
  font-size:  30px;
}

.card {
  color:#333;
  border-radius: 8px;
  /* box-shadow: 2px 2px 5px #888888; */
  background-color:#fff;
  padding: 10px;
  width:350px;
  margin:  auto;
  height: 200px;
  overflow-y: hidden;
  margin-bottom:25px;
  border:solid #ccc 1px;
}

.card:hover{
  box-shadow: 2px 2px 4px #888888;
}
.add_button{
  /* color:#333; */
  /* padding: 10px; */
  width:350px;
  margin:  auto;
  margin-bottom:25px;
}

.card-button {
  margin: 10px;
}

.card_title_input {
  background-color:transparent;
  height:30px;
  width:100%;
  outline:none;
  border:none;
  font-size: 16px;
  font-weight: 600;
  letter-spacing: 1px;
  font-family: 'Roboto';
}

.message_text{
  /* padding: 2px 1px;  */
  width:100%;
  outline: none;
  border-radius: 4px;
  font-size: 14px;
  line-height: 1.5;
  border: none;
  outline: none;
  resize: none;
  /* margin: 1px 4px;  */
  font-family: 'Roboto';
}
.card_title_div{
  border-bottom: solid #eee 1px;
  /* padding: 5px; */
  margin-bottom: 10px;
}
